___CSS_LOADER_EXPORT___.push([module.id, ".btn[data-v-3b21bef4]{padding:5px 40px!important}.text-justify[data-v-3b21bef4]{text-align:justify}.bg-img[data-v-3b21bef4]{background-repeat:no-repeat;background-size:cover;height:470px;max-width:700px}.p-size[data-v-3b21bef4]{font-size:18px}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Publication.vue"],"names":[],"mappings":"AAqHA,sBACA,0BACA,CACA,+BACI,kBACJ,CACA,yBAEI,2BAA4B,CAD5B,qBAAsB,CAGtB,YAAa,CADb,eAEJ,CACA,yBACI,cACJ","sourcesContent":["<template lang=\"html\">\n    <div>\n        <div class=\"container-fluid about-banner\">\n            <div class=\"container\">\n                <div class=\"row justify-center align-center text-center content-2\">\n                    <div class=\"col-lg-6 col-md-8 text-light\">\n                        <h2><b>{{ card.title }}</b></h2>\n                        <p>{{ card.subheading }}</p>\n                    </div>\n                </div>\n            </div>\n        </div>\n\n        <div class=\"border-pub mx-lg-3 mx-xl-3 mx-xxl-5 mx-4 my-5 pb-5\">\n            <div class=\"row justify-content-center align-items-stretch\">\n                <h4 class=\"py-4 text-center pb-4 color-blue\">Please fill the details to receive download link</h4>\n                <div class=\"p-lg-5 p-md-5 p-4 flex col-12 publication-card align-items-stretch\">\n                    <div class=\"text-light align-items-center\">\n                        <h3 class=\"text-center\">{{ card.form_header }}</h3>\n                        <div class=\"py-5 row justify-content-evenly\">\n                            <div v-if=\"card.image\" class=\"col-xl-6 col-xxl-5 col-lg-12 col-md-12 col-12 border border-3 bg-img\" :style=\"{ 'background-image': 'url(' + '/' + card.image +')'}\">\n<!--                                <img :src=\"'/' + card.image\" style=\"max-width:100%\" alt=\"\" v-if=\"card.image\"/>-->\n                            </div>\n                            <div class=\"py-3 col-xl-6 col-xxl-7 col-lg-12 col-md-12 col-12 p-size\">\n                                <p class=\"text-justify\">{{ card.form_subheading }}</p>\n                                <p class=\"text-justify\">{{ card.form_subheading2 }}</p>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col-lg-8 col-xl-3 col-xxl-3 col-md-12 col-12 contact-form pt-lg-5\">\n                    <form class=\"row g-3 p-2 mt-4\" name=\"fromPublication\" @submit.prevent=\"submit\">\n                        <div class=\"col-md-12\">\n                            <label for=\"\" class=\"form-label\">Name</label>\n                            <input type=\"text\" class=\"form-control\" name=\"name\" v-model=\"form.name\" required>\n                        </div>\n                        <div class=\"col-md-12\">\n                            <label for=\"\" class=\"form-label\">Email</label>\n                            <input type=\"email\" class=\"form-control\" name=\"email\" v-model=\"form.email\" required>\n                        </div>\n                        <div class=\"col-12\">\n                            <label for=\"\" class=\"form-label\">Phone</label>\n                            <input type=\"number\" class=\"form-control\" name=\"phone\" v-model=\"form.phone\">\n                        </div>\n                        <div class=\"col-12\">\n                            <label for=\"\" class=\"form-label\">Company</label>\n                            <input type=\"text\" class=\"form-control\" name=\"company\" v-model=\"form.company\">\n                        </div>\n                        <div class=\"mb-3\">\n                            <input type=\"checkbox\" id=\"agree\" name=\"agree\" v-model=\"form.agree\">\n                            <label for=\"agree\" style=\"display:inline;margin-left: 5px;\">By filling and submitting this form, you understand and agree that the use of ValueXPA website and its contents is subject to the <router-link :to=\"{name: 'terms'}\" target=\"_blank\">Terms and Conditions</router-link>. Additional details regarding our collection and use of your personal information can be seen in <router-link :to=\"{name: 'privacy'}\" target=\"_blank\">Privacy Policy</router-link>.</label>\n                        </div>\n                        <div class=\"col-12\">\n                            <button type=\"submit\" class=\"btn btn-secondary-2 px-5\">Send</button>\n                        </div>\n                    </form>\n                </div>\n            </div>\n        </div>\n\n        <Footer/>\n    </div>\n</template>\n<script>\nimport Footer from \"../components/Footer.vue\"\nimport axios from \"axios\"\nexport default {\n    components:{\n        Footer,\n    },\n    data() {\n        return {\n            form: {\n                name: null,\n                email: null,\n                phone: null,\n                company: null,\n                agree: null,\n                publication_id: null\n            },\n            card: []\n        }\n    },\n    methods: {\n        async loadData() {\n            let slug = this.$route.params.slug;\n\n            axios.get(\"/api/publications/\" + slug).then((res) => {\n                this.card = res.data;\n                this.form.publication_id = res.data.id;\n            });\n        },\n        submit()\n        {\n            axios.post('/api/form/publication', this.form)\n            .then((data) => {\n                alert('Details successfully submitted');\n                document.forms.fromPublication.reset();\n            })\n            .catch(function (error) {\n                if ( error.response.data.errors )\n                {\n                    let errorMsg = error.response.data.message;\n                    for (let prop in error.response.data.errors) {\n                        errorMsg += \"\\n\" + prop + \": \" + error.response.data.errors[prop];\n                    }\n                    alert(errorMsg);\n                }\n            });\n        },\n    },\n    mounted() {\n        this.loadData();\n    }\n}\n</script>\n<style lang=\"css\" scoped>\n.btn {\npadding: 5px 40px !important;\n}\n.text-justify {\n    text-align: justify;\n}\n.bg-img{\n    background-size: cover;\n    background-repeat: no-repeat;\n    max-width: 700px;\n    height: 470px;\n}\n.p-size{\n    font-size: 18px;\n}\n</style>\n"],"sourceRoot":""}]);
